}
.page {
  position: relative;
  width: 100%;
  height: 100%;
}
.wrapper {
  position: relative;
  width: 700px;
  padding: 15px;
  margin: 0 auto;
}
.wrapper .title {
  height: 80px;
  padding-top: 50px;
  line-height: 30px;
  width: 100%;
  color: #5B1112;
  font-size: 14px;
  font-weight: bold;
  border-bottom: 1px solid #5b1112;
}
.content-chart {
  position: absolute;
  left: 0;
  right: 0;
  top: 80px;
  bottom: 0;
  width: 100%;
}
@-webkit-keyframes slideup {
0% {
    -webkit-transform: translate(0, 0.1rem);
            transform: translate(0, 0.1rem);
}
50% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    -webkit-transform: translate(0, -0.1rem);
            transform: translate(0, -0.1rem);
}
}
@keyframes slideup {
0% {
    -webkit-transform: translate(0, 0.1rem);
            transform: translate(0, 0.1rem);
}
50% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    -webkit-transform: translate(0, -0.1rem);
            transform: translate(0, -0.1rem);
}
}
.slide-up-arrow {
  position: absolute;
  z-index: 100;
  bottom: 50px;
  width: 30px;
  left: 50%;
  margin-left: -15px;
  -webkit-animation: slideup 1s  linear infinite alternate;
          animation: slideup 1s  linear infinite alternate;
}



