
.china-map .wrapper {
  height: 100%;
}
.china-map .wrapper .chart {
  width: 100%;
  height: 100%;
}
.china-map svg {
  position: relative;
  z-index: 1000;
}
.china-map .map {
  position: absolute;
  z-index: 1;
}
.china-map .margin-top {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;
}
.china-map {
  width: 100%;
  height: 100%;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
}
.china-map svg {
    width: 100%;
    max-width: 100%;
}
.china-map .rank {
  font-size: 10px;
  width: 100%;
  height: 0.3rem;
  line-height: 0.3rem;
  border-bottom: 1px solid #5B1112;
  background: url("../../assets/img/map-category.png") center center no-repeat;
  background-size: 100%;
}
.china-map .map-chart {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
  background-size: 100%;
}
.china-map .map-area {
  height: 0.5rem;
  line-height: 0.5rem;
  width: 100%;
  padding: 0 0.25rem;
  font-size: 10px;
  color: #5B1112;
}
.other-label {
  fill: #5b1112;
}
.st0 {
  fill: #5B1111;
  stroke: #FFFFFF;
  stroke-width: 0.5;
  stroke-miterlimit: 10;
}
.st1 {
  fill-rule: evenodd;
  clip-rule: evenodd;
  fill: #5B1111;
  stroke: #FFFFFF;
  stroke-width: 0.5;
  stroke-miterlimit: 10;
}
.st2 {
  fill: #5B1111;
}
.st3 {
  fill: none;
  stroke: #FFFFFF;
  stroke-width: 0.5;
  stroke-miterlimit: 10;
}
.st4 {
  display: none;
}
.st5 {
  display: inline;
  fill: #EBEBEB;
}
.st6 {
  display: inline;
  fill: #FFFFFF;
}
.st7 {
  display: inline;
  fill: #B0B0B0;
}
.st8 {
  display: inline;
  fill: #757575;
}
.st9 {
  display: inline;
  fill: #666666;
  stroke: #FFFFFF;
  stroke-miterlimit: 10;
}
.st10 {
  display: inline;
  fill: #4D4D4D;
}
.st11 {
  display: inline;
  fill: #999999;
  stroke: #FFFFFF;
  stroke-miterlimit: 10;
}
.st12 {
  display: inline;
  fill: none;
  stroke: #000000;
  stroke-miterlimit: 10;
}



